.containerCards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}


.recipe {
    list-style: none;
}

.infos img {
    width: 80%;
    height: 30vh;
    border-radius: 1vw;
}

.infos {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 1vw;
    border: 1px solid black;
    border-radius: 1vw;
    max-width: 20vw;
    height: 50vh;
}

.infos:hover {
    scale: 1.10;
    background: whitesmoke;
}



.infos a,
.infos h2,
.infos h4 {
    display: flex;
    text-align: center;
    justify-content: center;
    max-width: 15vw;

}

.infos a {
    margin-bottom: 5vh;
    color: black;
    font-weight: bold;
}

.infos a:hover {
    scale: 1.20;
    color: teal;
}

.infos h4 {
    background: teal;
    border: 1px solid black;
    padding: 0.5vw;
    border-radius: 2vw;
}

.infos h2 {
    margin-top: 5vh;
    margin-bottom: 0;
    text-decoration: overline;
}

.logo {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.logo img {
    width: 15vw;
    height: auto;
}

.searchBarContainer {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
}

.searchBarContainer input {
    width: 20vw;
    border-radius: 1vw;
    height: 4vh;
    background: whitesmoke;
}